@import '_variables.scss';
#wpcontent {
  padding-left: 0;
}
.update-nag {
  display: none;
}
#screen-meta-links {
  position: absolute; right: 0; margin-top: 83px;

  .show-settings {
    background: #23282d;
    color: #fff;
    box-shadow: none;
    border: none;
    border-radius: 0;

    &:after {
      color: #fff;
    }

    &:hover, &:focus {
      color: $cluevo-teal;
      border: none;
      box-shadow: none;
    }
  }
}
#screen-meta {
  border: none;
  background: #23282d;
  margin-right: 0;
}

#contextual-help-wrap {
  background: #23282d;
  border: none;
}

#contextual-help-wrap.no-sidebar #contextual-help-back {
  border: none;
}

#contextual-help-back {
  background: #23282d;
}

.contextual-help-tabs-wrap {
  background: #23282d;
  color: #FFFFFF;
  padding-right: 0;

  .help-tab-content {
    margin-right: 0;
  }

  h1, h2, h3, h4, h5, h6 {
    color: #FFFFFF;
    padding-left: 5px;
  }

  h2 {
    border-left: 2px solid $cluevo-pink;
  }

  h3 {
    border-left: 2px solid $cluevo-purple;
  }

  h4 {
    border-left: 2px solid $cluevo-teal;
  }

  h5, h6 {
    border-left: 2px solid $cluevo-light-gray;
  }
}
.contextual-help-tabs {

  .active {
    border-left-color: $cluevo-pink;
    background: $cluevo-gray;
    a {
      border: none;
      color: #FFFFFF;
    }
  }
  ul {
    li {
      a {
        color: #FFFFFF;
        &:hover {
          color: $cluevo-pink;
        }
      }
    }
  }
}
